:root {
  --green: rgb(28, 205, 90);
  --greenDark: rgb(25, 185, 81);
  --greenLight: rgb(31, 225, 99);
}

.IndividualVideos {
  /* margin-top: 5rem; */

  display: flex;
  justify-content: center;
  width: 100%;
}

.IndividualVideos .videosInput {
  width: 50%;
  min-width: 300px;
  border: 4px solid var(--green);
  border-radius: 3px;
  overflow: hidden;
  /* padding: 0.5rem; */
}

.videosInput input {
  width: 80%;
  /* height: 100%; */
  height: 2rem;
  border: none;
  outline: none;
  padding: 0;
  padding-left: 0.5rem;
  box-sizing: border-box;
}

.videosInput button {
  width: 20%;
  height: 3rem;
  background-color: var(--green);
  color: white;
  border: none;
  /* box-sizing: border-box; */
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}

.videosInput button:hover {
  background-color: var(--greenDark);
}

.videosInput button.disabled {
  background-color: var(--greenLight);
  /* background-color: black; */
  cursor: default;
}

.videosInput button.disabled:hover {
  background-color: var(--greenLight);
}
