:root {
  --green: rgb(28, 205, 90);
  --greenDark: rgb(25, 185, 81);
}

.Download {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
}

.Download button {
  background-color: var(--green);
  border: none;
  transition: all 0.2s ease-in-out;
  color: white;
  font-size: 2rem;
  padding: 0.5rem;
  cursor: pointer;
}

.Download button:hover {
  background-color: var(--greenDark);
}
