.mgtop {
  margin-top: 1rem;
}

:root {
  --green: rgb(28, 205, 90);
  --greenDark: rgb(25, 185, 81);
  --greenLight: rgb(31, 225, 99);
}

.Playlist {
  margin-top: 5rem;

  display: flex;
  justify-content: center;
  width: 100%;
}

.Playlist .playlistInput {
  width: 50%;
  min-width: 300px;
  border: 4px solid rgb(28, 205, 90);
  border: 4px solid var(--green);
  border-radius: 3px;
  overflow: hidden;
  /* padding: 0.5rem; */
}

.playlistInput input {
  width: 80%;
  /* height: 100%; */
  height: 2rem;
  border: none;
  outline: none;
  padding: 0;
  padding-left: 0.5rem;
  box-sizing: border-box;
}

.playlistInput button {
  width: 20%;
  height: 3rem;
  background-color: rgb(28, 205, 90);
  background-color: var(--green);
  color: white;
  border: none;
  /* box-sizing: border-box; */
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}

.playlistInput button.disabled {
  background-color: rgb(31, 225, 99);
  background-color: var(--greenLight);
  /* background-color: black; */
  cursor: default;
}

.playlistInput button:hover {
  background-color: rgb(25, 185, 81);
  background-color: var(--greenDark);
}

.playlistInput button.disabled:hover {
  background-color: rgb(31, 225, 99);
  background-color: var(--greenLight);
}

